.header__bg:before {
  content: '';
  position: absolute;
  display: block;
  width: 872px;
  height: 100%;
  right: 0;
  top: 0;
  background: url(../img/header_bg-2.png) no-repeat 0 0;
  background-size: contain;
}
.header {
  width: 100%;
  height: 100%;
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 45px 0;
  overflow: hidden;
}
.header__inner {
  float: right;
  display: block;
  width: 100%;
  max-width: 700px;
  padding: 0 70px;
  text-align: right;
}
.header__logo {
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  text-transform: uppercase;
  color: #e1e1e1;
  font-size: 36px;
}
.header__logo:before {
  position: absolute;
  display: block;
  content: "";
  width: 111px;
  height: 25px;
  background: url(../img/sprites.png) 0 0;
  top: -9px;
  left: -9px;
}
.header__logo:after {
  position: absolute;
  display: block;
  content: "";
  width: 111px;
  height: 25px;
  background: url(../img/sprites.png) 0 -33px;
  bottom: -9px;
  left: -9px;
}
.header__nav {
  display: inline-block;
  position: relative;
  text-align: left;
  margin-left: 110px;
}
.header__item:last-child {
  margin-bottom: 0;
}
.header__link {
  position: relative;
  display: block;
  padding: 10px 0;
  font-size: 16px;
  text-transform: uppercase;
  color: #e1e1e1;
  text-decoration: none;
}
.header__link:before {
  position: absolute;
  display: none;
  content: "";
  width: 5px;
  height: 25px;
  top: 5px;
  left: -8px;
  background: url(../img/sprites.png) 16px -4px;
}
.header__link:hover.header__link:before {
  display: block;
}
.burger {
  display: none;
